import React, {memo, useCallback, useEffect, useRef} from 'react';
import {
  Fonts,
  goBack,
  styled,
  ToastService,
  useAutoToastError,
  useBoolean,
  useNavigationParams,
} from '@true/core';
import Animated, {
  runOnUI,
  useAnimatedRef,
  useDerivedValue,
  useSharedValue,
} from 'react-native-reanimated';
import {ICreateLetter, initCreateLetter} from '@/store/letters/type';
import {
  AnimatedKeyboardAvoidingViewRef,
  MyKeyboardAvoidingView,
  ScreenWrapper,
  UIInput,
  UITextInput,
} from '@true/ui';
import {AnimatedBottomSpace} from '@/components/AnimatedBottomSpace';
import {DiscardDialog} from '@/components/DiscardDialog';
import {ImageDTO} from '@/components/CameraRollPicker/useGallery';
import {IBase64, uploadImage} from '@/utils/helper';
import {useAsyncFn} from '@/hooks/useAsyncFn';
import {requestCreateLetter, requestEditLetter} from '@/store/letters/function';
import {getLetters} from '@/store/letters';
import {Buffer} from 'buffer';
import {MyInputBackground} from '@/screens/CreateEventScreen/MyInputBackground';
import {HeaderCreatePost} from '@/screens/CreatePostScreen/HeaderCreatePost';
import {CheckboxWithText} from '@/components/CheckboxWithText';
import LoadingModal from '@/components/LoadingModal';

export interface CreateLetterScreenParams {
  id: number;
}

export const CreateLetterScreen = memo(function CreateLetterScreen() {
  const {id} = useNavigationParams<CreateLetterScreenParams>();

  const params = useSharedValue<ICreateLetter>(initCreateLetter);
  const ref = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const scrollRef = useAnimatedRef<any>();
  const imageRef = useRef<ImageDTO | null>(null);

  const [visibleDialog, showDialog, hideDialog] = useBoolean();

  const onSelectBackground = useCallback(
    (image: ImageDTO) => {
      imageRef.current = image;
      params.value['image'] = image.url;
    },
    [imageRef, params],
  );
  const [{loading, error: createError}, onPressSubmit] =
    useAsyncFn(async () => {
      if (id !== -1) {
        await requestEditLetter(params.value);
      } else {
        await requestCreateLetter(params.value);
        ToastService.show('Create letter successfully');
      }
      goBack();
    }, [id, params]);
  useAutoToastError(createError);

  const onGoBack = useCallback(() => {
    console.log('ss');
    goBack();
  }, []);

  useEffect(() => {
    let _params = {
      ...initCreateLetter,
    };
    if (id > 0) {
      const letter = getLetters(id);
      const coverImage: IBase64 = JSON.parse(
        Buffer.from(
          letter?.image ||
            'eyJ0eXBlIjoiIiwiY29sb3IiOiIiLCJvcGFjaXR5IjowLCJwZXJjZW50IjoxMDAsImltYWdlIjoiIiwidGV4dHMiOltdfQ',
          'base64',
        ).toString('utf-8') || '',
      );
      _params = {
        id: letter?.id,
        content: letter?.content || '',
        name: letter?.name || '',
        image: letter?.image,
        files: letter?.files,
        notify: letter?.notify,
        notify_email: letter?.notify_email,
        disable_comment: letter?.disable_comment,
      };
    }
    params.value = _params;
  }, [id]);
  const animatedActiveBtn = useDerivedValue(() => {
    return !!params.value.name;
  }, []);
  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value) => {
        'worklet';

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [params],
  );
  return (
    <ScreenWrapper>
      <MyKeyboardAvoidingView ref={ref} scrollRef={scrollRef}>
        <HeaderCreatePost
          onPress={onPressSubmit}
          title={'Create a board letter'}
          active={animatedActiveBtn}
          id={id}
          loading={loading}
          onGoBack={onGoBack}
        />
        <LoadingModal isVisible={loading} />
        <SScrollView ref={scrollRef} keyboardShouldPersistTaps={'handled'}>
          <SContainer>
            <SField>
              <MyInputBackground
                onSelectedImage={onSelectBackground}
                image={params.value.image}
              />
              <UIInput.Text
                title={'Title'}
                keyName={'name'}
                params={params}
                placeholder={'---'}
                defaultValue={''}
                onChangeValue={onChangeValue}
                required
              />
              <UITextInput
                title={'Content'}
                keyName={'content'}
                params={params}
                defaultValue={''}
                onChangeValue={onChangeValue}
              />
            </SField>
            <SField>
              <STextBold>Advanced settings</STextBold>
              <CheckboxWithText
                params={params}
                keyName={'draft'}
                title={'Save as a draft'}
              />
              <CheckboxWithText
                params={params}
                keyName={'disable_comment'}
                title={'Disable comments'}
              />
              <CheckboxWithText
                params={params}
                keyName={'notify'}
                title={'Send notification'}
              />
              <CheckboxWithText
                params={params}
                keyName={'notify_email'}
                title={'Send email to everyone'}
              />
              <AnimatedBottomSpace />
            </SField>
          </SContainer>
        </SScrollView>
        <DiscardDialog
          title={'Discard change'}
          content={
            'You have made changes to this item that have not been saved. If you leave this page, your changes will be lost.'
          }
          visible={visibleDialog}
          onClose={hideDialog}
        />
      </MyKeyboardAvoidingView>
    </ScreenWrapper>
  );
});

const SContainer = styled(Animated.View)`
  flex: 1;
  padding: 12px 16px;
  gap: 12px;
`;

const SField = styled.View`
  gap: 12px;
  background-color: ${p => p.theme.white};
`;

const STextBold = styled.Text`
  font-family: ${Fonts.Medium};
  font-size: 16px;
  line-height: 24px;
  color: ${p => p.theme.neutral1};
`;
const SScrollView = styled.ScrollView`
  flex: 1;
  background-color: white;
  margin-top: 8px;
`;
