import {memo} from 'react';
import {FlexCenter} from '@/utils/helper';
import {goBack, styled, useTheme} from '@true/core';
import {Icons} from '@true/asset';
import {UIButton, UIText, UITextStyle} from '@true/ui';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import Animated, {
  interpolateColor,
  SharedValue,
  useAnimatedProps,
  useAnimatedStyle,
} from 'react-native-reanimated';

interface HeaderCreatePostProps {
  active: SharedValue<boolean>;
  onPress: () => void;
  loading: boolean;
  id?: number;
  onGoBack?: () => void;
  title: string;
}

export const HeaderCreatePost = memo(function (props: HeaderCreatePostProps) {
  const {aTop} = useAnimatedSafeAreaInsets();
  const animatedPaddingTop = useAnimatedStyle(() => ({
    paddingTop: aTop.value,
  }));
  const theme = useTheme();
  const {title, active, id, onPress, loading, onGoBack} = props;

  const animatedBtnStyle = useAnimatedStyle(() => {
    return {
      backgroundColor: interpolateColor(
        active.value ? 1 : 0,
        [0, 1],
        [theme.neutral3, theme.primaryColor],
      ),
      width: '100%',
    };
  }, [theme]);

  const animatedProps = useAnimatedProps(() => {
    return {
      pointerEvents: active.value ? 'auto' : 'none',
    };
  });

  // useAutoToastError(createError);
  return (
    <HeaderWrapper style={animatedPaddingTop}>
      <FlexCenter gap={8}>
        <UIButton
          source={Icons.Outlined.CHEVRON_LEFT}
          onPress={onGoBack ? onGoBack : goBack}
        />
        <UIText.HeaderLarge>{title}</UIText.HeaderLarge>
      </FlexCenter>
      <Animated.View animatedProps={animatedProps as any}>
        <SCreateBtn
          title={id ? 'Update' : 'Create'}
          onPress={onPress}
          style={animatedBtnStyle}
        />
      </Animated.View>
    </HeaderWrapper>
  );
});
const HeaderWrapper = styled(Animated.View)`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-bottom-color: ${p => p.theme.neutral4};
  border-bottom-width: 1px;
  padding: 6px 16px;
  background-color: white;
`;

const SCreateBtn = styled(UIButton).attrs(p => ({
  textStyle: {
    ...UITextStyle.body_medium_500,
    color: '#fff',
  },
}))`
  width: 94px;
  height: 32px;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
  padding: 4px 16px;
  background-color: ${p => p.theme.primaryColor};
`;
